import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Breadcrumbs from "@mui/material/Breadcrumbs";

const dispatchedEvents = [];

const SrsMuiBreadcrumbs = function (props, ref) {
  return (
    <Breadcrumbs
      separator={props.separator}
      maxItems={props.maxItems}
      aria-label={props["aria-label"]}
      expandText={props.expandText}
      collapseText={props.collapseText}
      onClick={props.onClick}
      theme={props.muiTheme}
    >
      <slot name="default" />
    </Breadcrumbs>
  );
};

const styles = `
  /* Custom styles for the MuiBreadcrumbs component */
`;

const props = [
  {
    name: "separator",
    defaultValue: "/",
    type: "string",
  },
  {
    name: "maxItems",
    defaultValue: null,
    type: "number",
  },
  {
    name: "aria-label",
    defaultValue: null,
    type: "string",
  },
  {
    name: "expandText",
    defaultValue: "Show path",
    type: "string",
  },
  {
    name: "collapseText",
    defaultValue: "Hide path",
    type: "string",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-breadcrumb"],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiBreadcrumbs), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
 
